import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { auth, db } from '../../../../firebase';
import { doc, getDoc, collection, addDoc, getDocs, query, orderBy, setDoc, deleteDoc, updateDoc } from 'firebase/firestore';
import Modal from 'react-modal';
import s from './taskDetail.module.css';
import close from './close.svg';

// Установите корневой элемент модального окна
Modal.setAppElement('#root');

export default function TaskDetail() {
  const { taskId } = useParams(); // Получаем taskId из URL
  const [task, setTask] = useState(null);
  const [subtasks, setSubtasks] = useState([]);
  const [newSubtask, setNewSubtask] = useState('');
  const [subtaskNumber, setSubtaskNumber] = useState('');
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [formIsOpen, setFormIsOpen] = useState(false);
  const [selectedSubtask, setSelectedSubtask] = useState(null);
  const [subtaskQuestions, setSubtaskQuestions] = useState([]);
  const [question, setQuestion] = useState('');
  const [options, setOptions] = useState([{ id: 1, text: '', isCorrect: false }]);
  const [correctOption, setCorrectOption] = useState(1);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [resultMessage, setResultMessage] = useState('');
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [isFinished, setIsFinished] = useState(false);

  useEffect(() => {
    const fetchTask = async () => {
      try {
        const taskRef = doc(db, 'tasks', taskId);
        const taskSnap = await getDoc(taskRef);
        if (taskSnap.exists()) {
          setTask({ id: taskSnap.id, ...taskSnap.data() });
        } else {
          console.log('No such document!');
        }
      } catch (error) {
        console.error('Error fetching task:', error);
      }
    };

    fetchTask();
  }, [taskId]);

  useEffect(() => {
    const fetchSubtasks = async () => {
      try {
        const subtasksCollection = collection(db, `tasks/${taskId}/subtasks`);
        const q = query(subtasksCollection, orderBy('number', 'asc'));
        const subtasksSnapshot = await getDocs(q);
        const subtasksList = subtasksSnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        }));
        setSubtasks(subtasksList);
      } catch (error) {
        console.error('Error fetching subtasks:', error);
      }
    };

    fetchSubtasks();
  }, [taskId]);

  const handleAddSubtask = async (event) => {
    event.preventDefault();
    if (subtaskNumber.trim() === '' || newSubtask.trim() === '') return;

    try {
      const subtasksCollection = collection(db, `tasks/${taskId}/subtasks`);
      await addDoc(subtasksCollection, {
        number: parseInt(subtaskNumber),
        description: newSubtask,
      });

      setSubtaskNumber('');
      setNewSubtask('');
      const subtasksSnapshot = await getDocs(query(subtasksCollection, orderBy('number', 'asc')));
      const subtasksList = subtasksSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
      }));
      setSubtasks(subtasksList);
    } catch (error) {
      console.error('Error adding subtask:', error);
    }
  };

  const openModal = async (subtask) => {
    setSelectedSubtask(subtask);
    setResultMessage('');  // Сбрасываем сообщение о результате
    setScore(0);  // Сбрасываем счет
    setIsFinished(false);  // Убираем состояние завершенности
  
    try {
      const resultRef = doc(db, `tasks/${taskId}/subtasks/${subtask.id}/results`, 'result');
      const resultSnap = await getDoc(resultRef);
  
      if (resultSnap.exists()) {
        const { score, totalQuestions } = resultSnap.data();
        setScore(score);
        setResultMessage(`Ваш результат: ${score} из ${totalQuestions}`);
        setIsFinished(true);
      } else {
        const questionsCollection = collection(db, `tasks/${taskId}/subtasks/${subtask.id}/questions`);
        const questionsSnapshot = await getDocs(questionsCollection);
        const questionsList = questionsSnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        }));
        setSubtaskQuestions(questionsList);
        setCurrentQuestionIndex(0);
        setSelectedAnswer(null);  // Сбрасываем выбранный ответ
      }
    } catch (error) {
      console.error('Error fetching questions or results:', error);
    }
  
    setModalIsOpen(true);
  };
  

  const openForm = (subtask) => {
    setSelectedSubtask(subtask);
    setFormIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setSelectedSubtask(null);
    setSubtaskQuestions([]);
    setCurrentQuestionIndex(0);
    setSelectedAnswer(null);
  };

  const closeForm = () => {
    setFormIsOpen(false);
    setQuestion('');
    setOptions([{ id: 1, text: '', isCorrect: false }]);
    setCorrectOption(1);
  };

  const handleAddQuestion = async (event) => {
    event.preventDefault();
    if (question.trim() === '' || options.some(option => option.text.trim() === '')) return;

    try {
      const questionsCollection = collection(db, `tasks/${taskId}/subtasks/${selectedSubtask.id}/questions`);
      await addDoc(questionsCollection, {
        question,
        options,
      });

      const questionsSnapshot = await getDocs(questionsCollection);
      const questionsList = questionsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
      }));
      setSubtaskQuestions(questionsList);

      setQuestion('');
      setOptions([{ id: 1, text: '', isCorrect: false }]);
      setCorrectOption(1);
      closeForm();
    } catch (error) {
      console.error('Error adding question:', error);
    }
  };

  const handleOptionChange = (index, value) => {
    const updatedOptions = options.map((option, i) => i === index ? { ...option, text: value } : option);
    setOptions(updatedOptions);
  };

  const addOption = () => {
    setOptions([...options, { id: options.length + 1, text: '', isCorrect: false }]);
  };

  const handleCorrectOptionChange = (index) => {
    const updatedOptions = options.map((option, i) => ({
      ...option,
      isCorrect: i === index,
    }));
    setOptions(updatedOptions);
    setCorrectOption(index + 1);
  };

  const handleAnswerSelection = (optionId) => {
    setSelectedAnswer(optionId);
  };

  const checkAnswer = async () => {
    if (!selectedAnswer) {
      setResultMessage('Выберите вариант ответа.');
      return;
    }
  
    const currentQuestion = subtaskQuestions[currentQuestionIndex];
    const correctOptionId = currentQuestion?.options.find(o => o.isCorrect)?.id;
  
    if (!correctOptionId) {
      setResultMessage('Нет правильного ответа для проверки.');
      return;
    }
  
    if (selectedAnswer === correctOptionId) {
      setResultMessage('Правильный ответ!');
      setScore(prevScore => prevScore + 1);
    } else {
      setResultMessage('Неправильный ответ. Попробуйте еще раз.');
    }
  
    if (currentQuestionIndex < subtaskQuestions.length - 1) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
      setSelectedAnswer(null);
    } else {
      const finalScore = score + (selectedAnswer === correctOptionId ? 1 : 0);
      setResultMessage(`Вы ответили на все вопросы. Ваш результат: ${finalScore} из ${subtaskQuestions.length}`);
      setIsFinished(true);
  
      try {
        await setDoc(doc(db, `tasks/${taskId}/subtasks/${selectedSubtask.id}/results`, 'result'), {
          score: finalScore,
          totalQuestions: subtaskQuestions.length,
          date: new Date(),
        });
  
        // Зачисление 1 рубля на баланс пользователя
        const userId = auth.currentUser?.uid; // Получите ID текущего пользователя
        if (userId) {
          await updateUserBalance(userId, 1); // Зачисление 1 рубля
        }
      } catch (error) {
        console.error('Error saving result:', error);
      }
    }
  };

  const handleRetry = async () => {
    try {
      await setDoc(doc(db, `tasks/${taskId}/subtasks/${selectedSubtask.id}/results`, 'result'), {
        score: 0,
        totalQuestions: subtaskQuestions.length,
        date: new Date(),
      });

      setScore(0);
      setCurrentQuestionIndex(0);
      setResultMessage('');
      setIsFinished(false);
    } catch (error) {
      console.error('Error retrying test:', error);
    }
  };

  const updateUserBalance = async (userId, amount) => {
    try {
      const userRef = doc(db, 'users', userId);
      const userSnap = await getDoc(userRef);
  
      if (userSnap.exists()) {
        const currentBalance = userSnap.data().balance || 0;
        await updateDoc(userRef, { balance: currentBalance + amount });
      } else {
        console.log('User not found');
      }
    } catch (error) {
      console.error('Error updating user balance:', error);
    }
  };

  const handleDeleteSubtask = async (subtaskId) => {
    try {
      // Создаем ссылку на документ подзадачи
      const subtaskRef = doc(db, `tasks/${taskId}/subtasks`, subtaskId);
      
      // Удаляем подзадачу
      await deleteDoc(subtaskRef);
      
      // Обновляем список подзадач после удаления
      const subtasksCollection = collection(db, `tasks/${taskId}/subtasks`);
      const q = query(subtasksCollection, orderBy('number', 'asc'));
      const subtasksSnapshot = await getDocs(q);
      const subtasksList = subtasksSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
      }));
      setSubtasks(subtasksList);
    } catch (error) {
      console.error('Error deleting subtask:', error);
    }
  };

  return (
    <div className={s.taskDetail}>
      {task ? (
        <>
          <div className={s.taskDetailTitle}>
            <h1 className={s.taskTitle}>{task.moduleNumber} модуль</h1>
            <p className={s.taskDescription}>{task.description}</p>
          </div>
  
          {/* Список подзадач */}
          <ul className={s.subtaskList}>
            {subtasks.map(subtask => (
              <li key={subtask.id} className={s.subtaskItem}>
                <span onClick={() => openModal(subtask)} className={s.subtaskText}>
                  {subtask.number}. {subtask.description}
                </span>
                <button onClick={() => openForm(subtask)} className={s.addQuestionButton}>Добавить задание</button>
                <button onClick={() => handleDeleteSubtask(subtask.id)} className={s.deleteButton}>Удалить</button>
              </li>
            ))}
          </ul>
  
          {/* Форма для добавления подзадачи */}
          <form onSubmit={handleAddSubtask} className={s.subtaskForm}>
            <input
              type="number"
              value={subtaskNumber}
              onChange={(e) => setSubtaskNumber(e.target.value)}
              placeholder="Номер подзадачи"
              className={s.inputNumber}
              required
            />
            <input
              type="text"
              value={newSubtask}
              onChange={(e) => setNewSubtask(e.target.value)}
              placeholder="Описание подзадачи"
              className={s.inputText}
              required
            />
            <button type="submit" className={s.addButton}>Добавить подзадачу</button>
          </form>
  
          {/* Модальное окно для отображения заданий */}
          <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            contentLabel="Задания"
            className={s.modal}
            overlayClassName={s.overlay}
          >
            <div>
            <img onClick={closeModal} className={s.closeButton} src={close} alt="" />
              {subtaskQuestions.length > 0 && (
                <div>
                  <div className={s.modalUpInfo}>
                    <p className={s.modalUpInfoTitle}>{currentQuestionIndex + 1}. Вопрос</p>
                  </div>
                  <div className={s.modalLine}></div>
                  <div className={s.modalUp}>
                    <p className={s.modalUpTitle}>
                      {subtaskQuestions[currentQuestionIndex]?.question}
                    </p>
                  </div>
                  <div className={s.modalLine}></div>
                  <ul className={s.optionsList}>
                    {subtaskQuestions[currentQuestionIndex]?.options?.map((opt, i) => (
                      <li key={i}>
                        <button
                          onClick={() => handleAnswerSelection(opt.id)}
                          className={
                            selectedAnswer === opt.id 
                              ? (opt.isCorrect ? s.selectedCorrect : s.selectedIncorrect) 
                              : s.optionButton
                          }
                        >
                          {opt.text}
                        </button>
                      </li>
                    ))}
                  </ul>
                  {!isFinished && (
                      <button onClick={checkAnswer} className={s.submitAnswerButton}>Ответить</button>
                    )}      
                </div>
              )}
            </div>
            {resultMessage && <p className={s.resultMessage}>{resultMessage}</p>}
          </Modal>
  
          {/* Форма добавления задания */}
          <Modal
            isOpen={formIsOpen}
            onRequestClose={closeForm}
            contentLabel="Создать задание"
            className={s.modal}
            overlayClassName={s.overlay}
          >
            <h2>Создание задания для подзадачи: {selectedSubtask?.description}</h2>
            <form onSubmit={handleAddQuestion} className={s.questionForm}>
              <div className={s.formGroup}>
                <label htmlFor="question" className={s.label}>Вопрос:</label>
                <input
                  type="text"
                  id="question"
                  value={question}
                  onChange={(e) => setQuestion(e.target.value)}
                  placeholder="Введите вопрос"
                  className={s.inputText}
                  required
                />
              </div>
  
              {/* Варианты ответов */}
              {options.map((option, index) => (
                <div key={option.id} className={s.optionGroup}>
                  <input
                    type="text"
                    value={option.text}
                    onChange={(e) => handleOptionChange(index, e.target.value)}
                    placeholder={`Вариант ${index + 1}`}
                    className={s.inputText}
                    required
                  />
                  <input
                    type="radio"
                    id={`correctOption${index}`}
                    name="correctOption"
                    checked={correctOption === option.id}
                    onChange={() => handleCorrectOptionChange(index)}
                    className={s.radioButton}
                  />
                  <label htmlFor={`correctOption${index}`} className={s.radioLabel}>Правильный ответ</label>
                </div>
              ))}
              <button type="button" onClick={addOption} className={s.addOptionButton}>Добавить вариант</button>
              <button type="submit" className={s.submitButton}>Добавить задание</button>
            </form>
            <button onClick={closeForm} className={s.closeButton}>Закрыть</button>
          </Modal>
        </>
      ) : (
        <p>Загрузка...</p>
      )}
    </div>
  );
};