import React, { useState, useRef, useEffect } from 'react';
import { signInWithEmailAndPassword, createUserWithEmailAndPassword, updateProfile, onAuthStateChanged, signOut } from 'firebase/auth';
import { auth, db } from '../firebase'; // Добавьте db для Firestore
import { doc, getDoc, setDoc } from "firebase/firestore";
import s from './header.module.css';

export default function Header() {
  const [showForm, setShowForm] = useState(false);
  const [isLogin, setIsLogin] = useState(true); // Флаг для переключения между входом и регистрацией
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState(''); // Имя
  const [lastName, setLastName] = useState(''); // Фамилия
  const [phone, setPhone] = useState(''); // Номер телефона
  const [error, setError] = useState('');
  const [user, setUser] = useState(null); // Состояние для отслеживания текущего пользователя
  const [balance, setBalance] = useState(0); // Состояние для хранения баланса
  const formRef = useRef(null);

  const handleButtonClick = () => {
    setShowForm(!showForm);
    setIsLogin(true); // По умолчанию отображаем форму входа
  };

  const handleCloseForm = () => {
    setShowForm(false);
  };

  const handleLogin = (e) => {
    e.preventDefault();
    signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        const user = userCredential.user;
        console.log('User signed in:', user);
        setShowForm(false);
      })
      .catch((error) => {
        setError('Ошибка входа: ' + error.message);
      });
  };

  const handleRegister = async (e) => {
    e.preventDefault();
    try {
      const userCredential = await createUserWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;
      
      await updateProfile(user, { displayName: `${name} ${lastName}` });
      console.log('User registered and profile updated:', user);
      
      // Сохранение данных пользователя в Firestore с балансом
      await setDoc(doc(db, 'users', user.uid), {
        firstName: name,
        lastName: lastName,
        phone: phone,
        email: email,
        balance: 0 // Устанавливаем начальный баланс равным 0
      });
      
      setShowForm(false);
    } catch (error) {
      setError('Ошибка регистрации: ' + error.message);
    }
  };

  const handleLogout = () => {
    signOut(auth)
      .then(() => {
        console.log('User signed out');
        setUser(null);
        setBalance(0); // Сброс баланса при выходе
      })
      .catch((error) => {
        console.error('Sign out error:', error);
      });
  };

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
        // Получение данных пользователя из Firestore
        const userDoc = await getDoc(doc(db, 'users', currentUser.uid));
        if (userDoc.exists()) {
          setBalance(userDoc.data().balance || 0);
        } else {
          console.error('No such document!');
        }
      } else {
        setUser(null);
        setBalance(0);
      }
    });

    return () => unsubscribe();
  }, []);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (formRef.current && !formRef.current.contains(event.target)) {
        setShowForm(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  return (
    <div className={s.header}>
      <div className={s.con}>
        <h1 className={s.headerTitleText}><b>&#123;</b> КодВеба <b>&#125;</b></h1>
        {user ? (
          <div className={s.userInfo}>
            <span className={s.userInfoBalance}> {balance} 🎫</span>
            <button className={s.btn} onClick={handleLogout}>Выйти</button>
          </div>
        ) : (
          <div className={s.userInfo}>
            <button className={s.btn} onClick={handleButtonClick}>Войти</button>
          </div>
        )}
        
      </div>

      {showForm && !user && ( // Показываем форму только если пользователь не авторизован
        <div className={s.formOverlay}>
          <div className={s.formContainer} ref={formRef}>
            <button className={s.closeButton} onClick={handleCloseForm}>×</button>

            {isLogin ? (
              <form className={s.form} onSubmit={handleLogin}>
                <h2>Форма входа</h2>
                {error && <p className={s.errorMessage}>{error}</p>}
                <label>
                  Логин:
                  <input 
                    type="email" 
                    value={email} 
                    onChange={(e) => setEmail(e.target.value)} 
                    required 
                  />
                </label>
                <label>
                  Пароль:
                  <input 
                    type="password" 
                    value={password} 
                    onChange={(e) => setPassword(e.target.value)} 
                    required 
                  />
                </label>
                <button type="submit">Войти</button>
                <p className={s.toggleForm}>
                  Нет аккаунта? <span onClick={() => setIsLogin(false)}>Зарегистрироваться</span>
                </p>
              </form>
            ) : (
              <form className={s.form} onSubmit={handleRegister}>
                <h2>Форма регистрации</h2>
                {error && <p className={s.errorMessage}>{error}</p>}
                <label>
                  Имя:
                  <input 
                    type="text" 
                    value={name} 
                    onChange={(e) => setName(e.target.value)} 
                    required 
                  />
                </label>
                <label>
                  Фамилия:
                  <input 
                    type="text" 
                    value={lastName} 
                    onChange={(e) => setLastName(e.target.value)} 
                    required 
                  />
                </label>
                <label>
                  Номер телефона:
                  <input 
                    type="tel" 
                    value={phone} 
                    onChange={(e) => setPhone(e.target.value)} 
                    required 
                  />
                </label>
                <label>
                  Логин (email):
                  <input 
                    type="email" 
                    value={email} 
                    onChange={(e) => setEmail(e.target.value)} 
                    required 
                  />
                </label>
                <label>
                  Пароль:
                  <input 
                    type="password" 
                    value={password} 
                    onChange={(e) => setPassword(e.target.value)} 
                    required 
                  />
                </label>
                <button type="submit">Зарегистрироваться</button>
                <p className={s.toggleForm}>
                  Уже есть аккаунт? <span onClick={() => setIsLogin(true)}>Войти</span>
                </p>
              </form>
            )}
          </div>
        </div>
      )}
    </div>
  );
}
