import React, { useEffect, useState } from 'react';
import { Route, Routes, useNavigate, Navigate } from 'react-router-dom';
import './App.css';
import { onAuthStateChanged } from 'firebase/auth';
import { auth } from './firebase'; // Подключаем Firebase
import Header from './Header/Header';
import Home from './Home/Home';
import Main from './Main/Main';

function App() {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true); // Добавляем состояние загрузки
  const navigate = useNavigate();

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      setLoading(false); // Завершаем состояние загрузки, когда получили пользователя
    });

    return () => unsubscribe(); // Отписываемся от события при размонтировании компонента
  }, []);

  if (loading) {
    return <div>Loading...</div>; // Пока идет проверка аутентификации, показываем загрузку
  }

  return (
    <div className="App">
      <Header />
      <Routes>
        {/* Если пользователь авторизован, перенаправляем его с Home на профиль */}
        <Route
          path="/"
          element={user ? <Navigate to="/profile" replace /> : <Home />}
        />
        {/* Показываем профиль только для авторизованных пользователей */}
        <Route
          path="/profile/*"
          element={user ? <Main /> : <Navigate to="/" replace />}
        />
      </Routes>
    </div>
  );
}

export default App;
