import React from 'react';
import s from './schedule.module.css';

export default function Schedule() {
  const year = 2024;
  const month = 8; // Сентябрь (месяцы считаются с 0)
  const daysInMonth = new Date(year, month + 1, 0).getDate(); // Количество дней в месяце
  const startDay = new Date(year, month, 1).getDay(); // День недели первого числа месяца

  const emptyDays = Array.from({ length: (startDay === 0 ? 6 : startDay - 1) }, (_, i) => i);
  const days = Array.from({ length: daysInMonth }, (_, i) => i + 1);

  const daysInPrevMonth = new Date(year, month, 0).getDate();
  const prevMonthDays = Array.from({ length: emptyDays.length }, (_, i) => daysInPrevMonth - emptyDays.length + i + 1);

  const events = [
    { date: 1, time: '10:00', topic: 'Математика' },
    { date: 3, time: '14:00', topic: 'Физика' },
    { date: 5, time: '16:00', topic: 'Химия' },
    { date: 7, time: '09:00', topic: 'Биология' },
    // Добавьте свои события сюда
  ];

  return (
    <div className={s.schedule}>
      <div className={s.scheduleDate}>
        <p className={s.scheduleDateNum}>Сентябрь 2024</p>
      </div>
      <div className={s.scheduleWeeks}>
        <p className={s.scheduleWeek}>Понедельник</p>
        <p className={s.scheduleWeek}>Вторник</p>
        <p className={s.scheduleWeek}>Среда</p>
        <p className={s.scheduleWeek}>Четверг</p>
        <p className={s.scheduleWeek}>Пятница</p>
        <p className={s.scheduleWeek}>Суббота</p>
        <p className={s.scheduleWeek}>Воскресенье</p>
      </div>
      <div className={s.scheduleDays}>
        {prevMonthDays.map(day => (
          <div key={`prev-${day}`} className={`${s.scheduleDay} ${s.prevMonthDay}`}>
            <p className={s.day}>{day}</p>
          </div>
        ))}
        {days.map(day => {
          const event = events.find(e => e.date === day);
          return (
            <div key={day} className={s.scheduleDay}>
              <p className={s.day}>{day}</p>
              {event && (
                <div className={s.event}>
                  {event.time} - {event.topic}
                </div>
              )}
            </div>
          );
        })}
      </div>
    </div>
  );
}
