import React from 'react'
import s from './navbar.module.css'
import { Link } from 'react-router-dom'
import calendar from './Calendar_Week.svg'
import profile from './User_Circle.svg'
import code from './File_Code.svg'

export default function Navbar() {
  return (
    <div className={s.navbar}>
      <Link to='/profile/myprofile' className={s.navbarItem}>
        <img className={s.navbarItemSvg} src={profile} alt="" />
        <p className={s.navbarItemText}>Профиль</p>
      </Link>
      <Link to='/profile/schedule'  className={s.navbarItem}>
        <img className={s.navbarItemSvg} src={calendar} alt="" />
        <p className={s.navbarItemText}>Расписание</p>
      </Link>
      <Link to='/profile/tasks' className={s.navbarItem}>
        <img className={s.navbarItemSvg} src={code} alt="" />
        <p className={s.navbarItemText}>Д/з</p>
      </Link>
    </div>
  )
}
