import React from 'react';
import { useNavigate } from 'react-router-dom';
import s from './carttask.module.css';
import clock from './Clock.svg';

export default function CartTask({ task }) {
  const navigate = useNavigate(); // Хук для навигации

  const handleCardClick = () => {
    navigate(`/profile/tasks/${task.id}`); // Переход на страницу задачи
  };

  return (
    <div className={s.cart} onClick={handleCardClick} style={{ cursor: 'pointer' }}>
      {task.imageUrl && (
        <div className={s.cartImage}>
          <img src={task.imageUrl} alt="Task" />
        </div>
      )}
      <div className={s.cartUp}>
        <h1 className={s.cartTitle}>{task.moduleNumber} модуль</h1>
        <div className={s.cartDecision}>
          <img className={s.cartDecisionSvg} src={clock} alt="clock" />
        </div>
      </div>
      <div className={s.cartBackMirror}>
        <p>{task.description}</p>
      </div>
    </div>
  );
}
