import React, { useState, useEffect } from 'react';
import { doc, getDoc, updateDoc } from 'firebase/firestore';
import { ref, uploadBytes, getDownloadURL } from 'firebase/storage'; // Импорт для работы с Firebase Storage
import { db, auth, storage } from '../../../firebase'; // Импортируйте storage
import s from './profile.module.css';
import a from './back.jpg'; // Стандартное изображение, если нет загруженного
import b from './profile.png';
import set from './Settings.svg';


export default function Profile() {
  const [userData, setUserData] = useState(null);
  const [isEditing, setIsEditing] = useState(false);
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [coverUrl, setCoverUrl] = useState(a); // По умолчанию отображаемое изображение

  useEffect(() => {
    const fetchUserData = async () => {
      const user = auth.currentUser;
      if (user) {
        try {
          const docRef = doc(db, 'users', user.uid);
          const docSnap = await getDoc(docRef);
          
          if (docSnap.exists()) {
            const data = docSnap.data();
            setUserData(data);
            setFirstName(data.firstName);
            setLastName(data.lastName);
            if (data.coverUrl) {
              setCoverUrl(data.coverUrl); // Установить обложку, если она уже загружена
            }
          } else {
            console.log('No such document!');
          }
        } catch (error) {
          console.error('Error fetching user data: ', error);
        }
      }
    };

    fetchUserData();
  }, []);

  const handleEditClick = () => {
    setIsEditing(true);
  };

  const handleSaveClick = async () => {
    if (auth.currentUser) {
      const userRef = doc(db, 'users', auth.currentUser.uid);
      try {
        await updateDoc(userRef, {
          firstName,
          lastName,
        });
        setUserData({ ...userData, firstName, lastName });
        setIsEditing(false);
      } catch (error) {
        console.error('Error updating user data: ', error);
      }
    }
  };

  const handleCoverChange = async (e) => {
    if (e.target.files[0] && auth.currentUser) {
      const newCover = e.target.files[0];
      const storageRef = ref(storage, `covers/${auth.currentUser.uid}`);

      try {
        // Загрузка изображения в Firebase Storage
        await uploadBytes(storageRef, newCover);
        const downloadURL = await getDownloadURL(storageRef);

        // Обновление ссылки на изображение в Firestore
        const userRef = doc(db, 'users', auth.currentUser.uid);
        await updateDoc(userRef, {
          coverUrl: downloadURL,
        });

        // Обновление состояния для отображения новой обложки
        setCoverUrl(downloadURL);
      } catch (error) {
        console.error('Error uploading cover: ', error);
      }
    }
  };

  if (!userData) {
    return <p>Loading...</p>;
  }

  return (
    <div>
      <div className={s.profileContainer}>
        <div className={s.coverContainer}>
          <img src={coverUrl} alt="Cover" className={s.coverImage} />
          <button className={s.editCoverButton} onClick={() => document.getElementById('coverInput').click()}>Изменить обложку</button>
          <input
            type="file"
            id="coverInput"
            style={{ display: 'none' }}
            accept="image/*"
            onChange={handleCoverChange} // Изображение загружается сразу при выборе
          />
        </div>
        <div className={s.profileInfo}>
          <div className={s.avatarContainer}>
            <img src={b} alt="Avatar" className={s.avatar} />
          </div>
          <div className={s.details}>
            {isEditing ? (
              <>
                <input
                  type="text"
                  value={firstName}
                  onChange={(e) => setFirstName(e.target.value)}
                  className={s.input}
                />
                <input
                  type="text"
                  value={lastName}
                  onChange={(e) => setLastName(e.target.value)}
                  className={s.input}
                />
                <button className={s.saveButton} onClick={handleSaveClick}>Сохранить</button>
              </>
            ) : (
              <>
                <h2 className={s.username}>{`${userData.firstName} ${userData.lastName}`}</h2>
                <p className={s.role}>Ученик</p>
                <button className={s.editProfileButton} onClick={handleEditClick}>
                  <img src={set} alt="" />
                </button>
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
