import React from 'react';
import s from './home.module.css';
import a from './a.png'
import b from './b.png'
import c from './c.png'
import d from './d.png'
import img from './img.png'
import { Link } from 'react-router-dom';

export default function Home() {
  return (
    <div className={s.home}>
        <div className={s.con}>
      <div className={s.leftSide}>
        <div className={s.labels}>
          <span className={s.label}>Места ограничены</span>
          <span className={s.label}>1.990 руб</span>
          <span className={s.label}>Участие в розыгрыше</span>
        </div>
        <h1 className={s.leftSideTitle}>Начни свой путь в веб-разработку: курс по HTML</h1>
        <p className={s.desc}>
            Присоединяйтесь к курсу по HTML и изучайте веб-разработку в группе Discord. 
            Узнайте, как создавать веб-страницы с нуля, и погружайтесь в мир HTML вместе с единомышленниками. 
            Начните свой путь уже сейчас!
        </p>
        <ul className={s.benefits}>
            <h2 className={s.benefitsTitel}>На этом курсе вы:</h2>
          
            <div className={s.flex}>
                <div className={s.benefit}>
                    <img className={s.benefitImg} src={a} alt="" />
                    <li>Понимание основ HTML: теги, атрибуты и структура документа</li>
                </div>
                <div className={s.benefit}>
                  <img className={s.benefitImg} src={b} alt="" />
                  <li>Работа с ссылками, картинками и медиа в HTML</li>
                </div>
            </div>

            <div className={s.flex}>
                <div className={s.benefit}>
                    <img className={s.benefitImg} src={c} alt="" />
                    <li>Создание таблиц и форм для взаимодействия с пользователем</li>
                </div>
                <div className={s.benefit}>
                    <img className={s.benefitImg} src={d} alt="" />
                    <li>Использование семантических тегов для улучшения SEO</li>
                </div>
            </div>

          
        </ul>
        <div className={s.priceInfo}>
            <p className={s.price}>
              <b className={s.oldPrice}>8990</b> 1.990 руб
              </p>
              <Link className={s.priceBtn}>Купить</Link>
        </div>
      </div>
      <img className={s.img} src={img} alt="" />
      {/*<div className={s.rightSide}>
        <div className={s.form}>
          <div>
            <h3 className={s.formTitle}>Запись на курс</h3>
            <p className={s.rightSideDesc}>Осталось мест: 10</p>
          </div>
          <form>
            <input className={s.input} type="text" placeholder="Имя" />
            <input className={s.input} type="email" placeholder="Электронная почта" />
            <input className={s.input} type="tel" placeholder="+7 (999) 999-99-99" />
          </form>
            <button className={s.btn} type="submit">Записаться</button>
        </div>
      </div>*/}
      </div>
    </div>
  );
}
